.types__item {
  position: relative; }
  .types__item:not(:first-child) {
    margin-left: 30px; }
  .types__item:nth-child(3n+1) {
    padding-left: 60px;
    padding-top: 40px;
    z-index: 0; }
  .types__item:nth-child(3n+1)::before {
    content: "";
    position: absolute;
    width: 180px;
    height: 180px;
    top: 195px;
    left: 0;
    border-radius: 50%;
    border: 27px solid #0ad9c6; }
  .types__item:nth-child(3n-1) {
    padding-left: 19px;
    padding-right: 23px;
    padding-top: 70px; }
  .types__item:nth-child(3n-1)::before, .types__item:nth-child(3n-1)::after {
    content: "";
    position: absolute;
    width: 47px;
    height: 47px;
    z-index: 2;
    border: 8px solid #0ad9c6; }
  .types__item:nth-child(3n-1)::before {
    top: 50px;
    left: 0; }
  .types__item:nth-child(3n-1)::after {
    bottom: 30px;
    right: 0; }
  .types__item:nth-child(3n) {
    padding-right: 36px;
    padding-top: 30px; }
  .types__item:nth-child(3n)::before {
    content: "";
    position: absolute;
    width: 95px;
    height: 95px;
    top: 0;
    right: 0;
    z-index: 2;
    border-radius: 50%;
    border: 17px solid #0ad9c6; }
  @media (max-width: 1284px) {
    .types__item {
      width: 30%; } }
  @media (max-width: 1023px) {
    .types__item {
      width: auto; }
      .types__item:not(:first-child) {
        margin-left: 0; }
      .types__item:nth-child(3n+1) {
        padding-left: 37px;
        padding-top: 0; }
      .types__item:nth-child(3n+1)::before {
        width: 127px;
        height: 127px;
        top: 201px;
        border: 18px solid #0ad9c6; }
      .types__item:nth-child(3n-1) {
        padding: 0; }
      .types__item:nth-child(3n-1)::before, .types__item:nth-child(3n-1)::after {
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        border: none; }
      .types__item:nth-child(3n) {
        padding-right: 25px;
        padding-top: 20px;
        margin-top: 30px; }
      .types__item:nth-child(3n)::before {
        width: 68px;
        height: 68px;
        border: 13px solid #0ad9c6; } }
  @media (max-width: 767px) {
    .types__item {
      width: 100%; }
      .types__item:nth-child(3n+1) {
        margin-bottom: 40px;
        padding-left: 24px; }
      .types__item:nth-child(3n-1) {
        margin-bottom: 24px;
        padding: 0 24px; }
      .types__item:nth-child(3n) {
        padding-right: 23px;
        padding-top: 20px;
        margin-top: 0;
        padding-left: 25px; }
      .types__item:nth-child(3n)::before {
        right: 9px; } }

